@import url("https://fonts.googleapis.com/css?family=Dancing+Script|Josefin+Sans:300,300i,400|Nanum+Gothic|Raleway+Dots");

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #D0CDD7;
}

.center-text {
  height: 200px;
  color: #787B84;
  text-align: center;
  position: absolute;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  margin-top: auto;
  margin-bottom: auto;
}

p.company {
  font-family: 'Raleway Dots', cursive;
  color: #0B3948;
  margin: 0;
  /* padding: 0; */
}

p {
  font-family: 'Josefin Sans', Arial, sans-serif;
  font-weight: 300;
}

p.sub-company {
  color: #416165;
  margin-top: -5px;
}

a:link {
  color: #787B84;
  text-decoration: none;
}

a:hover{
  color: #416165;
}

p.company {
  font-size: 100px;
}

p.sub-company {
  font-size: 40px;
}

p.text {
  font-size: 25px;
}



